import React from 'react'
import MainListItem from './MainListItems.js'

const MainList = ({gifs}) => {
  const giphList = gifs.map((gif, i) =>
    <div key={i}>
        <MainListItem gif={gif} key={gif.id} h1={gif.title}/>     
    </div>
  )

  return (
    <div className="flexContent">
      {giphList}
    </div>
  )
}

export default MainList;


